"use client";
import React, { useState, useRef } from "react";
import gsap from "gsap";

const Services = () => {
  const [openIndex, setOpenIndex] = useState(null); // Track which accordion is open
  const boxRefs = useRef([]); // Store refs for each accordion item

  const toggleAccordion = (index) => {
    const currentRef = boxRefs.current[index];
    const isOpen = currentRef.style.height !== "0px"; // Check if it's already open

    if (isOpen) {
      gsap.to(currentRef, {
        height: 0, // Collapse to 0 height
        opacity: 0, // Fade out
        duration: 0.5,
        ease: "power2.in",
        display: "none", // Hide content after collapsing
      });
    } else {
      gsap.to(currentRef, {
        height: "5rem", // Expand to the height of the content
        opacity: 1, // Fade in
        duration: 0.5,
        ease: "power2.out",
        display: "block", // Make sure content is visible during expansion
      });
    }

    // Toggle the open state
    setOpenIndex(isOpen ? null : index); // Close it if it's already open, else open it
  };

  // Accordion items data
  const accordionItems = [
    {
      title: "Digital Marketing",
      content:
        "We utilize advanced AI technology to optimize your marketing strategies and ensure sustained growth.",
    },
    {
      title: "Creative Branding & Design",
      content:
        "We offer expert graphic design, brand identity development, and videography services to help your brand stand out in a competitive market.",
    },
    {
      title: "Comprehensive Marketing Solutions",
      content:
        "A one-stop agency for all your digital marketing needs, including SEO, social media management, content creation, and digital advertising.",
    },
    {
      title: "Scalable & Remote-Friendly",
      content:
        "Flexible, scalable digital marketing strategies that grow with your business, whether you're a startup, small business, or enterprise.",
    },
  ];

  return (
    <div className="flex items-center justify-center h-screen">
      <div className="flex flex-col items-center w-11/12 gap-12">
        <div className="w-full flex justify-between">
          <div className="w-1/6">
            <p>/ Why Choose Us /</p>
          </div>
          <div className="w-4/5 flex justify-between">
            <div className="w-1/3">
              <h3 className="text-3xl font-medium">
                Crafting Growth through data-driven insights
              </h3>
            </div>
            <div className="w-3/5">
              <p className="leading-8">
                Many agencies offer similar services, but no one does it like
                us. With innovative strategies, AI-driven precision, and
                creative solutions, we make your brand stand out in a crowded
                market. At Vortex Media, we offer more than just marketing – we
                deliver results.
              </p>
            </div>
          </div>
        </div>

        <div className="flex flex-col items-center w-full">
          {/* Dynamic accordion items */}
          {accordionItems.map((item, index) => (
            <div
              className="flex flex-col w-full overflow-hidden gap-3"
              key={index}
            >
              {/* Header */}
              <div
                className="flex items-center w-full justify-between h-16 border-b cursor-pointer"
                onClick={() => toggleAccordion(index)} // Toggle based on the clicked index
              >
                <div className="w-1/6 flex items-center justify-between">
                  <p>{`0${index + 1}`}</p>
                  <span>{openIndex === index ? "-" : "+"}</span>
                </div>
                <div className="content w-3/4 md:w-[48%]">
                  <h4>{item.title}</h4>
                </div>
              </div>

              {/* Content (will expand when clicked) */}
              <div
                ref={(el) => (boxRefs.current[index] = el)} // Store refs for each item
                className="px-6 flex items-center overflow-hidden w-4/5 md:w-1/2 self-end"
                style={{ height: 0, opacity: 0, display: "none" }} // Start collapsed with zero height
              >
                <p>{item.content}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Services;
